import React from "react";
import { useSelector } from "react-redux";
import firebase from "gatsby-plugin-firebase";

import { EditMediaTableNoCategories } from "./MediaEditor";
import ApexEmptyOptionsChart from "./ApexEmptyOptionsChart";

import { GetReadyScatterApexChart } from "./ShowMedia";


//import { selectTempData, setCKEditorContent } from "../features/temp/tempSlice";
import { selectApplication } from "../features/application/applicationSlice";

import MarkDeleteFireNodeButton from "../components/MarkDeleteFireNodeButton";

import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";

export default function CreateOrShowScatterApexChartPostForProject({ firenode = null }) {
    if (!firenode) { return <CreateScatterApexChartPostForProject /> }
    return <ShowOrEditScatterApexChartPostForProject post={firenode} />
}


function ShowOrEditScatterApexChartPostForProject({ post }) {
    const email = useSelector(selectApplication).email;
    let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
    // const dispatch = useDispatch();
    const [open, setOpen] = React.useState(false);
  
    function openScatterApexChart() {
    //   dispatch(load_data({ protoData: createProtoArray(post.content) }));
    //   dispatch(set_spreadsheetTitle({ spreadsheetTitle: post.title }));
       setOpen(!open)
    }
  
   // console.log(post);
  
    return (
      <div className="m-2">
        <Button
         
          onClick={() => openScatterApexChart()}
          aria-controls={"Scatter-ApexChart-collapse" + post.id}
          aria-expanded={open}
          variant={open ? "secondary" : "outline-secondary"}
          size="sm"
          block
        >
          {post.title}
        </Button>
        <Collapse in={open}>
          <div id={"Scatter-ApexChart-collapse" + post.id}  className="m-2">
          <MarkDeleteFireNodeButton nodeRef={"usersCraft/" + userEmail + "/posts/"} id={post.id} />
          <GetReadyScatterApexChart firenode={post.content} />
          </div>
        </Collapse>
      </div>
    );
  }

function CreateScatterApexChartPostForProject() {
    const [series, setSeries] = React.useState([
        {
            name: "Данные",
            data: [
                [5, 5],
                [-5, 5],
                [-5, -5],
                [5, -5],
                [1, 1],
                [-2, -2],
            ],
        },
    ]);

    //  const ckEditorContent = useSelector(selectTempData).ckeditor_node_content;
    const email = useSelector(selectApplication).email;
    const user = useSelector(selectApplication).user;
    const avatarUrl = useSelector(selectApplication)?.avatarUrl;
    const currentProjectMediaAndDataAndTemplatesURL = useSelector(selectApplication).currentProjectMediaAndDataAndTemplatesURL;
    const currentProjectTitle = useSelector(selectApplication).currentProjectTitle;
    const currentProjectComment = useSelector(selectApplication).currentProjectComment;


    function doUpdateChartSeries(series) {
        console.log(series);
        setSeries(series);
        //      saveComment()
    }


    function saveComment() {
        let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        let idPost = firebase.database().ref(userEmail).child("posts").push().key;

        let postObject = {
            id: idPost,
            title: "Гистограмма",
            theme: currentProjectTitle,
            answer: currentProjectMediaAndDataAndTemplatesURL,
            comment: currentProjectComment,
            type: "scatterapexchart",
            content: { series: series[0].data },
            quizString: "",
            deleted: false,
            email: email,
            user: user,
            avatarUrl: !!avatarUrl ? avatarUrl : null,
            date: new Intl.DateTimeFormat("ru", {
                weekday: "short",
                year: "numeric",
                month: "short",
                day: "numeric",
                hour: "numeric",
                minute: "numeric",
            }).format(new Date()), //Date().toJSON()
        };

        console.log(postObject);

        var updates = {};
        updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
        updates["/openmedia/" + idPost] = postObject;

        return firebase
            .database()
            .ref()
            .update(updates)
            .then(() => {
                //       //      DoSavedSuccessfully(true);
                //       //      doEditPostTitleComment(false);
                setTimeout(window.location.reload(), 3000); //; props.handleClose()
            });
    }


    return (
        <Container className="mt-3">
            <EditMediaTableNoCategories
                series={series}
                doUpdateChartSeries={doUpdateChartSeries}
            />
            <ApexEmptyOptionsChart
                seriesWithNames={series}
                //   series={series[0].data}
                showAs="scatterapexchart"
            />
            <Button
                variant="outline-secondary"
                size="sm"
                className="m-2"
                onClick={() => saveComment()}
            >
                Сохранить
      </Button>
        </Container>
    );

}