import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import firebase from "gatsby-plugin-firebase";
//import { useFirebaseNode } from "../hooks/useFirebaseNode";
import ShowRegressionString, {
  regressionGradient,
} from "../laboratory/ShowRegressionString";
import HTMLEditorBallonBlock from "./HTMLEditorBallonBlock";

import ImportFinamQuotesCSVSpreadsheet from "../features/temp/ImportFinamQuotesCSVSpreadsheet";

import ApexEmptyOptionsChart from "../mediatemplates/ApexEmptyOptionsChart";
// import ScatterQuotesApexChart from "../mediatemplates/ScatterQuotesApexChart";
// import DatalineQuotesApexChart from "../mediatemplates/DatalineQuotesApexChart";

import { normalizeQuotes } from "../hooks/quotesFunctions";

import { selectApplication } from "../features/application/applicationSlice";

//import { selectUserData } from "../features/data/dataSlice";
import { selectTempData, emptyTempData } from "../features/temp/tempSlice";

import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";

import InputGroup from "react-bootstrap/InputGroup";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
import FormControl from "react-bootstrap/FormControl";

function StudentScatterChartBetaCompany(props) {
  //   const data = useSelector(selectUserData).data;
  const data = useSelector(selectTempData).dataArray;
  const [selectedAsset, setSelectedAsset] = useState(null);
  const [selectedIndex, setSelectedIndex] = useState(null);
  const [htmlfeedback, setHtmlfeedback] = useState("Комментарий");

  const [lineChartData, setLineChartData] = useState(null);
  const [scatterChartData, setScatterChartData] = useState(null);

  useEffect(() => {
    if (!!selectedAsset && !!selectedIndex) {
      let assetTickerArray = selectedAsset.split("_");
      let indexTickerArray = selectedIndex.split("_");
      if (assetTickerArray[1] === indexTickerArray[1]) {
        let datesIndexArrayRedux = data
          .filter(
            (item) =>
              item.datatype === "quote" &&
              item.ticker === indexTickerArray[0] &&
              item.per === indexTickerArray[1]
          )
          .map((quoteItem) => {
            let year = quoteItem.quotedate.substring(0, 4);
            let month = quoteItem.quotedate.substring(4, 6);
            let day = quoteItem.quotedate.substring(6, 8);
            let date = +new Date(year, month - 1, day);
            //        console.log(date);
            return [date, parseFloat(quoteItem.close)];
          });
        let datesAssetArrayRedux = data
          .filter(
            (item) =>
              item.datatype === "quote" &&
              item.ticker === assetTickerArray[0] &&
              item.per === assetTickerArray[1]
          )
          .map((quoteItem) => {
            let year = quoteItem.quotedate.substring(0, 4);
            let month = quoteItem.quotedate.substring(4, 6);
            let day = quoteItem.quotedate.substring(6, 8);
            let date = +new Date(year, month - 1, day);
            //        console.log(date);
            return [date, parseFloat(quoteItem.close)];
          });

        let series = [
          {
            name: indexTickerArray[0],
            data: [...datesIndexArrayRedux],
          },
          {
            name: assetTickerArray[0],
            data: [...datesAssetArrayRedux],
          },
        ];

        let normalizeQuotesData = normalizeQuotes(series);
        setLineChartData(normalizeQuotesData.series);
        setScatterChartData(normalizeQuotesData.scatterdata);
      }
    }
  }, [selectedAsset, selectedIndex]);

  if (!!lineChartData && !!scatterChartData && !!data && data.length > 0) {
    return (
      <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
        <Card.Header>Кейс</Card.Header>
        <Card.Body>
          <Card.Title>Расчет Beta коэффициента</Card.Title>
          <ImportFinamQuotesCSVSpreadsheet
            openButton={"Открыть CSV файл с данными по активу"}
          />
          <SelectAssetAndIndex
            setSelectedAsset={setSelectedAsset}
            setSelectedIndex={setSelectedIndex}
          />
          <ApexEmptyOptionsChart
            showAs={"datalinequotesapexchart"}
            series={lineChartData}
          />{" "}
          {/*  */}
          <ApexEmptyOptionsChart
            showAs={"scatterquotesapexchart"}
            series={scatterChartData}
          />{" "}
          {/*  */}
          <HTMLEditorBallonBlock
            htmlfeedback={htmlfeedback}
            setHtmlfeedback={setHtmlfeedback}
          />
          <hr />
          <CheckAnswer
            scatterChartData={scatterChartData}
            lineChartData={lineChartData}
            htmlfeedback={htmlfeedback}
          />
        </Card.Body>
      </Card>
    );
  }

  return (
    <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
      <Card.Header>Кейс</Card.Header>
      <Card.Body>
        <Card.Title>Расчет Beta коэффициента</Card.Title>
        <Card.Text>
          Для начала необходимо подгрузить файл с котировками с сайта finam.ru
        </Card.Text>
        <ImportFinamQuotesCSVSpreadsheet
          openButton={"Открыть CSV файл с данными по активу"}
        />
        {!!data && data.length > 0 ? (
          <SelectAssetAndIndex
            setSelectedAsset={setSelectedAsset}
            setSelectedIndex={setSelectedIndex}
          />
        ) : null}
      </Card.Body>
    </Card>
  );
}

export default StudentScatterChartBetaCompany;

function CheckAnswer(props) {
  const [value, setValue] = useState("");
  const [answerIsRight, setAnswerIsRight] = useState(null);
  const dispatch = useDispatch();
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;

  //  function handleChange(event) { setValue(event.target.value); }

  function handleCheckAnswer() {
    let res = regressionGradient(props.scatterChartData);
    if (
      parseFloat(value) / parseFloat(res) < 1.07 &&
      parseFloat(value) / parseFloat(res) > 0.93
    ) {
      setAnswerIsRight(true);

      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idData = firebase.database().ref(userEmail).child("data").push().key;
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let postObject = {
        id: idPost, //Math.floor(Math.random() * 1001),
        title: "Кейс Расчет Beta коэффициента", //props.title,
        theme: "Оценка финансовых рисков", // props.theme,
        answer: res,
        comment: "Beta коэффициент", // props.title + " (" + props.theme + ")", //Тема
        type: "media",
        content: idData,
        htmlfeedback: props.htmlfeedback,
        quizString:
          "Рассчитайте Beta коэффициент для выбранного актива относительно фондового индекса", //props.text,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : "",
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      let currentDay = new Intl.DateTimeFormat("en", {
        weekday: "short",
        year: "numeric",
        month: "short",
        day: "numeric",
      })
        .format(new Date())
        .replace(/[^a-zA-Z0-9]/g, "_");

      let currentDayObject = {
        id: idPost,
        title: "Кейс Расчет Beta коэффициента",
        theme: "Оценка финансовых рисков",
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        timestamp: +Date.now(),
      };

      let dataObject = {
        datalinequotesapexchart: {
          series: props.lineChartData,
          user: user,
          avatarUrl: avatarUrl,
        },
        scatterquotesapexchart: {
          series: props.scatterChartData,
          user: user,
          avatarUrl: avatarUrl,
        },
      };

      console.log(dataObject);

      var updates = {};
      updates[
        "/currentDay/" + currentDay + "/posts/" + idPost
      ] = currentDayObject;

      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/usersCraft/" + userEmail + "/data/" + idData] = dataObject;

      updates["/openmedia/" + idPost] = postObject;
      updates["/openmediadata/" + idData] = dataObject;
      return firebase.database().ref().update(updates);
    } else {
      dispatch(emptyTempData());
    }
  }

  return (
    <>
      <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
          <Button variant="outline-secondary" onClick={handleCheckAnswer}>
            Ответ
          </Button>
          <FormControl
            value={value}
            type="number"
            onChange={(event) => setValue(event.target.value)}
            aria-label="Answer"
            aria-describedby="inputGroup-answer"
          />
        </InputGroup.Prepend>
      </InputGroup>
      {answerIsRight ? (
        <Alert variant={"success"}>
          <ShowRegressionString arrayOfarrays={props.scatterChartData} />
        </Alert>
      ) : null}
    </>
  );
}

function SelectAssetAndIndex(props) {
  //  const data = useSelector(selectUserData).data;
  const data = useSelector(selectTempData).dataArray;
  const [selectedAsset, setSelectedAsset] = useState(null);
  const [selectedIndex, setSelectedIndex] = useState(null);
  //   console.log(data);

  let assetList = [];
  data.forEach((element) => {
    let hasCurrentElement = assetList.some(
      (item) => item.ticker === element.ticker && item.per === element.per
    );
    if (!hasCurrentElement) {
      assetList.push({ ticker: element.ticker, per: element.per });
    }
  });
  //   console.log(assetList);

  function doSelectAsset(e) {
    setSelectedAsset(e);
    props.setSelectedAsset(e);
  }

  function doSelectIndex(e) {
    setSelectedIndex(e);
    props.setSelectedIndex(e);
  }

  return (
    <div>
      <InputGroup className="m-2">
        <FormControl
          placeholder={selectedAsset ? selectedAsset : "Исследуемый актив"}
          aria-label="asset"
          aria-describedby="basic-asset"
        />

        <DropdownButton
          as={InputGroup.Append}
          variant="outline-secondary"
          title="Список"
          id="asset-group-dropdown"
        >
          {assetList.map((item) => (
            <Dropdown.Item
              key={item.ticker + "_" + item.per}
              eventKey={item.ticker + "_" + item.per}
              onSelect={(e) => doSelectAsset(e)}
            >
              {item.ticker + "_" + item.per}
            </Dropdown.Item>
          ))}
          {/* <Dropdown.Item href="#">Action</Dropdown.Item>
                <Dropdown.Divider />
                <Dropdown.Item href="#">Separated link</Dropdown.Item> */}
        </DropdownButton>
      </InputGroup>

      <InputGroup className="m-2">
        <FormControl
          placeholder={selectedIndex ? selectedIndex : "База - индекс"}
          aria-label="asset"
          aria-describedby="basic-asset"
        />

        <DropdownButton
          as={InputGroup.Append}
          variant="outline-secondary"
          title="Список"
          id="index-group-dropdown"
        >
          {assetList.map((item) => (
            <Dropdown.Item
              key={item.ticker + "_" + item.per}
              eventKey={item.ticker + "_" + item.per}
              onSelect={(e) => doSelectIndex(e)}
            >
              {item.ticker + "_" + item.per}
            </Dropdown.Item>
          ))}
        </DropdownButton>
      </InputGroup>
    </div>
  );
}
